  height: auto; }

.header__brand {
  font-size: 34px;
  letter-spacing: -1px;
  color: #ffffff;
  display: block; }
  @media (max-width: 576px) {
    .header__brand {
      display: none; } }

.header__list {
  display: flex; }

.header__item {
  list-style-type: none;
  margin: 0px 30px 0px 0px; }
  .header__item:last-child {
    margin: 0px 0px 0px 0px; }

.header__link {
  text-decoration: none;
  font-size: 14px;
  color: #ffffff;
  font-weight: 300;
  font-family: "Montserrat", sans-serif; }
  .header__link:hover {
    color: #c7b299; }

.header__info {
  display: flex; }

.header__cart {
  cursor: pointer; }
  @media (max-width: 320px) {
    .header__cart {
      display: none; } }

.header__search {
  cursor: pointer;
  margin: 0px 0px 0px 28px; }

.header__content {
  text-align: center;
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 0;
  position: relative;
  z-index: 3; }
  .header__content .slick-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0; }
  .header__content .slick-dots li {
    list-style-type: none;
    margin: 0px 5px; }
  .header__content .slick-dots button {
    font-size: 0;
    width: 10px;
    height: 10px;
    background-color: #8f8f91;
    border-radius: 50%;
    box-sizing: content-box;
    outline: none; }
  .header__content .slick-dots li.slick-active button {
    background-color: transparent;
    border: 2px solid #c4b3a1; }

.header__slider {
  min-width: 0;
  position: relative;
  padding: 0px 10px; }
  .header__slider .slick-arrow {
    position: absolute;
    top: 50%; }
    .header__slider .slick-arrow.arrow-left {
      z-index: 3;
      left: 15px;
      display: inline-block;
      width: 30px;
      height: 30px;
      border-left: 3px solid #d3c1ac;
      border-bottom: 3px solid #d3c1ac;
      transform: rotate(45deg) translateY(-50%);
      cursor: pointer; }
      @media (max-width: 767px) {
        .header__slider .slick-arrow.arrow-left {
          display: none; } }
    .header__slider .slick-arrow.arrow-right {
      z-index: 3;
      right: 15px;
      display: inline-block;
      width: 30px;
      height: 30px;
      border-right: 3px solid #d3c1ac;
      border-bottom: 3px solid #d3c1ac;
      transform: rotate(-45deg) translateY(-50%);
      cursor: pointer; }
      @media (max-width: 767px) {
        .header__slider .slick-arrow.arrow-right {
          display: none; } }

.header__slider-item {
  min-width: 0; }

.header__slider-subtitle span {
  font-size: 36px;
  color: #ffffff;
  font-weight: 500; }
  @media (max-width: 767px) {
    .header__slider-subtitle span {
      font-size: 30px; } }
  @media (max-width: 444px) {
    .header__slider-subtitle span {
      display: none; } }

.header__slider-title {
  margin: 22px 0px 28px 0px; }

.header__slider-description {
  margin: 0 auto;
  max-width: 562px; }
  .header__slider-description p {
    font-size: 16px;
    line-height: 24px;
    color: #ffffff;
    font-weight: 300;
    font-family: "Montserrat", sans-serif; }

.header__slider-btn {
  margin: 53px 0px 10px 0px; }

.title {
  font-size: 52px;
  color: #ffffff;
  font-weight: 800;
  margin: 0; }
  @media (max-width: 767px) {
    .title {
      font-size: 44px; } }
  @media (max-width: 380px) {
    .title {
      font-size: 36px; } }

.arrow-right.slick-arrow.slick-disabled {
  cursor: default;
  border-right: 3px solid #a1a09e;
  border-bottom: 3px solid #a1a09e; }

.arrow-left.slick-arrow.slick-disabled {
  cursor: default;
  border-left: 3px solid #a1a09e;
  border-bottom: 3px solid #a1a09e; }

@media (max-width: 991px) {
  .header__col {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 1 33.333%; }
    .header__col:nth-of-type(1) {
      order: 2; }
    .header__col:nth-of-type(2) {
      order: 1; }
    .header__col:nth-of-type(3) {
      order: 3; } }

.button {
  font-size: 14px;
  line-height: 23px;
  color: #ffffff;
  font-weight: 800;
  text-decoration: none;
  height: 50px;
  display: inline-flex;
  align-items: center;
  padding: 0px 39px 0px 37px;
  background-color: #988574;
  border-radius: 3px;
  text-transform: uppercase;
  -webkit-box-shadow: 0px 6px 0px 0px #726257;
  -moz-box-shadow: 0px 6px 0px 0px #726257;
  box-shadow: 0px 6px 0px 0px #726257; }

/*Саму шапку делаем фиксированой*/
/*Этот класс является основной, условно это контейнер внутри которого всё происходит*/
.header {
  position: relative;
  width: 100%;
  top: 0;
  left: 0; }

/*Этому-же класс мы делаем псевдоэлементом заливку цвета и определяем размер*/
.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2; }

/*Сам контейнер внутри которого расположени элементы меню*/
.header__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 80px;
  align-items: center;
  position: relative;
  z-index: 4; }

/*Логотип*/
.header__logo {
  position: relative;
  z-index: 4;
  overflow: hidden; }

.header__info {
  position: relative;
  z-index: 4; }

.header__logo img {
  max-width: 100%;
  display: block;
  height: auto; }

/*На стандартных экранах бургер спрятан*/
.header__burger {
  display: none; }

/*Стили для основного меню и его пунктов*/
.header__list {
  position: relative;
  z-index: 2; }

@media (max-width: 991px) {
  /*Чтобы не скролилась страница под бургером меню*/
  body.lock {
    overflow: hidden; }
  /*Делаем размер Шапки меньше*/
  .header__body {
    height: 50px; }
  /*Показываем бургер и задаём стили*/
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 4;
    cursor: pointer; }
  .header__burger:before,
  .header__burger:after {
    content: "";
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s; }
  .header__burger:before {
    top: 0; }
  .header__burger:after {
    bottom: 0; }
  .header__burger.active:before {
    transform: rotate(45deg);
    top: 9px; }
  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 9px; }
  .header__burger.active span {
    transform: scale(0); }
  .header__burger span {
    position: absolute;
    background-color: #fff;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s; }
  /*Стилизация меню*/
  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: #362f2d;
    padding: 120px 15px 20px 15px;
    transition: all 0.3s ease 0s;
    z-index: 3; }
  .header__menu.active {
    top: 0; }
  .header__list {
    display: block; }
  .header__list li {
    margin: 0px 0px 20px 0px; }
  .header__link {
    font-size: 24px; } }

